    @if $name==$default-color {
      background-image: buildTape($type, str-replace(to-string($color), '#', '%23'));
    }@else {
      &.tape--#{$name} {
        background-image: buildTape($type, str-replace(to-string($color), '#', '%23'));
      }
    }
  }
}

.tape {
  display: inline-block;
  min-width: rem-calc(206.5);
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;

  .tape {
    display: block;
  }

  @include tape();

  &__label {
    color: $white;
    font-size: rem-calc(14);
    text-align: center;
    white-space: nowrap;
    padding: rem-calc(10);
  }

  &--small {
    min-width: rem-calc(140);
    @include tape('small');
  }

  &--cross {
    min-width: 0;
    @include tape('cross');

    .tape__label {
      font-size: rem-calc(17);
      letter-spacing: rem-calc(-1);
      min-width: rem-calc(112);
      padding: rem-calc(21) rem-calc(10);

      @include breakpoint(medium) {
        font-size: rem-calc(19);
        min-width: rem-calc(160);
      }
    }
  }

  &--white .tape__label {
    color: $primary-color;
  }

  &--scroll {
    width: rem-calc(110);
    height: rem-calc(120);
    min-width: 0;
    cursor: pointer;

    @include tape('scroll');

    &.tape--positioned {
      position: absolute;
      margin: auto;
      right: 0;
      left: 0;
      margin-top: rem-calc(-61-15);
    }
  }

  &--double {
    @include tape('double');
    -ms-transform: rotate(-3deg);
    transform: rotate(-3deg);

    .tape__wrapper {
      transform: rotate(3deg);
      color: blue;
    }

    .tape__label {
      font-family: $alt-font;
      font-size: 15vw;
      line-height: 1;
      padding: 2.9vw 3.6vw;
      margin: 0 0 -5.1vw -1.45vw;

      @include breakpoint(450) {
        font-size: 20vw;
      }

      @include breakpoint(675) {
        font-size: rem-calc(135);
        padding: rem-calc(20) rem-calc(25);
        margin: 0 0 rem-calc(-35) rem-calc(-10);
      }
    }
  }

  &--large1 {
    @include tape('large1');
    max-width: rem-calc(485);
    -ms-transform: rotate(-3deg);
    transform: rotate(-3deg);

    .tape__label {
      font-family: $alt-font;
      font-size: 11vw;
      line-height: 1;
      padding: 4.2vw 0 0 4.8vw;
    }
    .tape__label2 {
      color: $white;
      font-size: 3.6vw;
      font-weight: bold;
      line-height: 1;
      padding: 0 5.6vw 4.4vw;
    }

    @include breakpoint(515) {
      .tape__label {
        font-size: rem-calc(57);
        padding: rem-calc(22) 0 0 rem-calc(25);
      }
      .tape__label2 {
        font-size: rem-calc(19);
        padding: 0 rem-calc(29) rem-calc(23);
      }

    }
  }

  &--large2 {
    @include tape('large2');

    @include breakpoint(350) {
      height: rem-calc(120);
    }

    .tape__label {
      font-family: $alt-font;
      font-size: 23vw;
      line-height: 1;
      padding: rem-calc(30) rem-calc(20) 0;

      @include breakpoint(350) {
        font-size: rem-calc(80);
        min-width: rem-calc(300);
        padding: rem-calc(30) rem-calc(20) 0;
      }
    }
  }

  &--large3 {
    @include tape('large3');

    @include breakpoint(550) {
      height: rem-calc(120);
    }

    .tape__label {
      font-family: $alt-font;
      font-size: 14.5vw;
      line-height: 1;
      padding: 5.4vw 4.5vw 0;

      @include breakpoint(550) {
        font-size: rem-calc(80);
        padding: rem-calc(30) rem-calc(25) 0;
        min-width: rem-calc(400);
      }
    }
  }

  .tape__wrapper {
    text-align: center;
    position: absolute;
    width: 100%;
    bottom: 0%;

    .tape__label {
      font-family: $base-font;
      font-size: 5vw;

      &--bold {
        font-weight: bolder;

      }
      @include breakpoint(400) {
        font-size: rem-calc(20);
      }
    }
  }
}